___CSS_LOADER_EXPORT___.push([module.id, `.hero-container {
    height: 100vh;
}

.hero-h1 {
    position: absolute;
    z-index: 2;
    text-align: center;
    font-size: 60px;
    font-family: 'saira', sans-serif;
    color: var(--light-blue);
    padding-top: 200px;
    max-width: 1140px ;
}

.hero-background {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100vh;
}

.hero-background img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.hero-img {
    position: absolute;
    inset: 0;
    z-index: 1;
    backdrop-filter: brightness(40%);
    width: 100%;
    height: 100%;
}

.purchase_hero_h1 {
    position: absolute;
    max-width: 1140px;
    z-index: 2;
    text-align: center;
    font-size: 60px;
    font-family: 'saira', sans-serif;
    color: var(--light-blue);
    padding-top: 300px;
